.contacts__wrapper {
  display: grid;
  grid-template-columns: 560px 1fr;
  align-items: center;
  padding-top: 120px;
  padding-bottom: 118px;

  @include vp-1023 {
    grid-template-columns: 324px 1fr;
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column-reverse;
    padding-top: 58px;
    padding-bottom: 63px;
  }
}

.contacts__map {
  width: 560px;
  height: 306px;

  line-height: 0;

  background-image: url("../img/content/map-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: $color-map;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/content/map-desktop@2x.jpg");
  }

  @include vp-1023 {
    width: 324px;
    height: 335px;

    background-image: url("../img/content/map-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/content/map-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    width: 100%;
    height: 316px;

    background-image: url("../img/content/map-mobile.jpg");
    background-position: top;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/content/map-mobile@2x.jpg");
    }
  }
}

.contacts__inner-wrapper {
  align-self: flex-start;
  padding: 74px 0 76px 100px;

  @media (max-width: $vp-1210) {
    padding-left: 15px;
  }

  @include vp-1023 {
    padding: 66px 0 46px 30px;
  }

  @include vp-767 {
    padding: 0 0 38px 0;
  }
}

.contacts h4 {
  margin-top: 0;
  margin-bottom: 33px;

  @include font-group-medium($size-18, $line-150);
  color: $color-tangaroa;

  @include vp-1023 {
    margin-bottom: 28px;
  }
}

.contacts__inner-wrapper p,
.contacts__inner-wrapper a {
  position: relative;

  margin-top: 0;
  padding-left: 28px;

  @include font-group($size-16, $line-140);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;

    width: 18px;
    height: 22px;

    background-repeat: no-repeat;
    background-position: 0 center;
  }

  @include vp-1023 {
    &::before {
      top: 4px;
    }
  }
}

.contacts__inner-wrapper p {
  margin-bottom: 21px;

  &::before {
    background-image: url("../img/sprite/icon-map-mark.svg");
  }

  @include vp-1023 {
    margin-bottom: 21px;
  }
}

.contacts__inner-wrapper a {
  margin-bottom: 0;

  &::before {
    top: -3px;

    background-image: url("../img/sprite/icon-phone.svg");
  }

  @include vp-1023 {
    &::before {
      top: -3px;
    }
  }
}
